  import { Component , Inject , OnInit} from '@angular/core';
  import {HttpClient} from '@angular/common/http';

  @Component({
    selector: 'app-root',
    templateUrl: './app.component.html',
    styleUrls: ['./app.component.scss']
  })
  export class AppComponent implements OnInit{
    title = 'myApp';
    name:string;
    country:string;
    list = [];


    constructor(@Inject('ps') private ps , private http:HttpClient){}

  ngOnInit(){
    this.getData()
  }
    onSubmit(){
      this.postData()
    }

    getData(){
      this.ps.getList().subscribe(data => this.list = data)
    }

    postData(){
      const data = {
        name:this.name,
        country:this.country
      }
      this.http.post(`http://localhost:3000/api/postdata` , data).subscribe(res => {
        console.log(res);

      })
      this.reset();
      this.getData();
    }

    reset(){
      this.name = '';
      this.country = '';
    }
  }
